*,
*::after,
*::before {
  box-sizing: border-box;
}

:root {
  font-size: 15px;
}

body {
  margin: 0;
  --color-text: #d8d8d8;
  --color-bg: #fff;
  --color-link: #fff;
  --color-link-hover: #fff;
  --color-line: rgba(82, 77, 73, 0.38);
  color: var(--color-text);
  background-color: var(--color-bg);
  font-family: termina, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.demo-2 {
  --color-text: #313131;
  --color-bg: #fff;
}

/* Page Loader */
.js .loading::before,
.js .loading::after {
  content: "";
  position: fixed;
  z-index: 1000;
}

.js .loading::before {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--color-bg);
}

.js .loading::after {
  top: 50%;
  left: 50%;
  width: 60px;
  height: 60px;
  margin: -30px 0 0 -30px;
  border-radius: 50%;
  opacity: 0.4;
  background: var(--color-link);
  animation: loaderAnim 0.7s linear infinite alternate forwards;
}

@keyframes loaderAnim {
  to {
    opacity: 1;
    transform: scale3d(0.5, 0.5, 1);
  }
}

a {
  text-decoration: underline;
  color: var(--color-link);
  outline: none;
}

a:hover,
a:focus {
  color: var(--color-link-hover);
  outline: none;
  text-decoration: none;
}

main {
  text-align: center;
  padding: 2rem;
}

/* Grainy texture animation by Geoff Graham https://css-tricks.com/snippets/css/animated-grainy-texture/ */

main::before {
  animation: grain 3s steps(5) infinite;
  background-image: url(../img/noise.png);
  content: "";
  height: 300%;
  left: -50%;
  opacity: 0.5;
  position: fixed;
  top: -100%;
  width: 300%;
  pointer-events: none;
}

@keyframes grain {
  0%,
  100% {
    transform: translate(0, 0);
  }
  10% {
    transform: translate(-5%, -10%);
  }
  20% {
    transform: translate(-15%, 5%);
  }
  30% {
    transform: translate(7%, -25%);
  }
  40% {
    transform: translate(-5%, 25%);
  }
  50% {
    transform: translate(-15%, 10%);
  }
  60% {
    transform: translate(15%, 0%);
  }
  70% {
    transform: translate(0%, 15%);
  }
  80% {
    transform: translate(3%, 35%);
  }
  90% {
    transform: translate(-10%, 10%);
  }
}

.navigation {
  grid-area: logo;
  text-decoration: none;
  font-size: 1rem;
  font-weight: 700;
  align-self: center;
  justify-self: center;
  color: #313131;
}

.artist-name {
	grid-area: pagetitle;
	margin: 0;
	font-weight: 600;
	font-size: 1rem;
	padding: 1rem 0;
  }

.page-title {
  grid-area: pagetitle;
  margin: 0;
  font-weight: 600;
  font-size: 1rem;
  padding: 1rem 0;
}

.page-title div {
  clip-path: polygon(0 0, var(--clip) 0, var(--clip) 100%, 0% 100%);
}


.links {
  grid-area: links;
  padding-top: 1rem;
}

.demos div:not(:first-child),
.links div:not(:first-child) {
  margin-left: 1rem;
}

.frame__demo {
	color: #313131;
  display: inline-block;
}

.frame__demo--current {
  opacity: 0.8;
  text-decoration: none;
  color: #313131;
}

.title {
  grid-area: title;
  font-family: dystopian, sans-serif;
  font-weight: 700;
  font-size: 11.25vw;
  margin: 0;
  line-height: 0.9;
  text-indent: -0.9vw;
}

.subtitle {
  grid-area: subtitle;
  font-size: 4vw;
  margin: 0.5rem 0 0 0;
  line-height: 1;
  font-weight: 200;
  text-indent: -0.4vw;
}

