.content {
  margin: 0;
  font-size: 1.15rem;
  font-size: clamp(1rem, 2vh, 3rem);
  grid-area: content;
  padding: 2rem 0;
}

.content span {
  clip-path: polygon(0 0, 100% 0, 100% var(--clip), 0% var(--clip));
}

.play {
  grid-area: play;
  font-size: 12vw;
  align-self: center;
  justify-self: center;
  cursor: default;
  display: block;
}

.emailLink {
  grid-area: year;
}

.credits--tinyText {
	grid-area: credits-2;
  }

.credits--contact {
  grid-area: credits-1;
  font-weight: 600;
  text-decoration: none;
}




.credits--author div {
  clip-path: polygon(0 0, var(--clip) 0, var(--clip) 100%, 0% 100%);
}

@media screen and (min-width: 60em) {
  main {
    text-align: left;
    padding: 0;
    overflow: hidden;
    height: 100vh;

	/* CSS Grid */
    display: grid;
    grid-template-columns: 8rem 12vh 9rem 1fr 1fr;
	/* define amount and size of each column */
	
    grid-template-rows: 4rem 4rem min-content min-content 1fr 2.5rem;
    grid-template-areas:
      "logo pagetitle ... ... ..."
      "logo ... ... demos ..."
      "... ... ... title title"
      "... ... ... ... subtitle"
      "... menu ... content play"
      "year ... credits-1 credits-2 ...";
  }
